import "bootstrap/dist/js/bootstrap.min.js";
import SidebarComponent from "@/components/Sidebar/index.vue";
import NavbarComponent from "@/components/Navbar/index.vue";
import EventEmitter from "@/util/EventEmitter";
import { defineComponent } from "vue";
export default defineComponent({
  components: {
    SidebarComponent,
    NavbarComponent,
  },
  mounted() {
    EventEmitter.emitter.on("sidebar-toggle-navbar", function (value: boolean) {
      EventEmitter.emitter.emit("sidebar-toggle-sidebar", value);
    });
  },
});
