import { defineComponent } from "vue";
import EventEmitter from "@/util/EventEmitter";
import { useStore } from "vuex";

export default defineComponent({
  name: "NavbarComponent",
  data() {
    return {
      sidebarToggle: false,
      context: process.env.VUE_APP_CONTEXT,
      appVersion: require("../../../lib/version"),
    };
  },
  setup() {
    const store = useStore();
    return {
      store,
    };
  },
  methods: {
    logout() {
      // @ts-expect-error test
      this.$auth.logout({
        returnTo: `${window.location.origin}${this.context}`,
      });
      this.store.commit("removeTemplate");
    },
    toggleMenu() {
      this.sidebarToggle = !this.sidebarToggle;
      EventEmitter.emitter.emit("sidebar-toggle-navbar", this.sidebarToggle);
    },
  },
});
